import * as React from "react";
import { graphql, PageProps } from "gatsby";
import Img from "gatsby-image";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import { SEO } from "../components/SEO";
import { GoogleFormProvider, useGoogleForm } from "react-google-forms-hooks";
import form from "../form.json";
import ShortAnswerInput from "../components/ShortAnswerInput";
import LongAnswerInput from "../components/LongAnswer";

const PartnerWithUsPage: React.FC<PageProps> = (props) => {
  const data = props.data as any;
  const backgroundImage = data.backgroundImage;

  const methods = useGoogleForm({ form: form as any });

  const onSubmit = async (data: any) => {
    data["1165880350"] = `Partnership Request:${data["1165880350"]}`;
    await methods.submitToGoogleForms(data);
    window.location.href = "/thank-you";
  };

  return (
    <main>
      <div className="relative">
        <Img
          fluid={backgroundImage.childImageSharp.fluid}
          alt={backgroundImage.name}
          className="!absolute z-[-1] w-full h-full !object-cover !object-center"
        />
        <div className="blur-background">
          <Navbar />
        </div>
        <GoogleFormProvider {...methods}>
          <form onSubmit={methods.handleSubmit(onSubmit)} autoComplete="off">
            <div
              className="py-16 bg-white text-[#262A2D] container select-none"
              id="services"
            >
              <h2 className="text-2xl">Partner with Us</h2>
              <p>Thank you for showing your interest in partnering with us!</p>
              <div className="mb-10"></div>
              <span className="block mb-3">Full Name</span>
              <ShortAnswerInput
                id="1862994178"
                required={true}
                type="text"
                className="border p-3 w-full lg:max-w-[50%]"
              />
              <div className="mb-4"></div>
              <span className="block mb-3">Email</span>
              <ShortAnswerInput
                id="1056499612"
                required={true}
                type="email"
                className="border p-3 w-full lg:max-w-[50%]"
              />
              <div className="mb-4"></div>
              <span className="block mb-3">Message</span>
              <LongAnswerInput
                id="1165880350"
                required={true}
                rows={4}
                className="border p-3 w-full lg:max-w-[50%]"
              />
              <div className="mb-10"></div>
              <button
                type="submit"
                className="bg-[#262A2D] text-white px-6 py-2 rounded-full"
              >
                Submit
              </button>
            </div>
          </form>
        </GoogleFormProvider>
      </div>
      <Footer />
    </main>
  );
};

export default PartnerWithUsPage;

export const Head = () => <SEO title={`Partner with Us | VPTronics`} />;

export const query = graphql`
  query {
    backgroundImage: file(relativePath: { eq: "solar-farm.jpg" }) {
      name
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
